'use strict';

import Saveable from 'module-crud-ui/app/scripts/crud.saveable.js';
import Pageable from 'module-crud-ui/app/scripts/crud.pageable.js';
/* global angular */

export default class MaterialDetailChapterRatingsCtrl extends Saveable {

  /*@ngInject*/
  constructor( $state, translateService,$injector) {

    super(
      {
        injector:$injector,
        endpoint:'materialChapterRatings',
        key:'chapterId',
        backToState:'materialDetail',
        params:{materialId:$state.params.materialId}
      }
    );


    this.state = $state;
    this.translateService = translateService;


    //TODO: Fix actions from card
    this.actions = [
      {
        tooltip:translateService.translate('tooltip.search'),
        action: () => { this.state.go('materialDetail', this.state.params); },
        icon: 'arrow-left'
      }
    ];
  }
}
